import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import './App.css';

//https://material-ui.com/components/text-fields/

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

function App() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [values, setValues] = React.useState({
    name: '',
    result: ''
  });

  function handleClickOpen() {
    setOpen(true);
  }

  function handleClose() {
    setOpen(false);
  }

  const predict = async (input) => {
    const url = 'http://35.230.6.132:8081/predict?input='+input;
    const response = await fetch(url);
    const json = await response.json();
    return json.result;
  }
  
  const handleChange = name => event => {
    setValues({ ...values, [name]: event.target.value });
  };

  const handleSubmit = async () => {
    console.log('Your input value is: ' + values.name);
    const result = await predict(values.name);
    console.log(result);
    setValues({ ...values, ['result']: result });
    handleClickOpen();
  }
  
  return (
    <div className="App">
      <header className="App-header">
        <form className={classes.container} noValidate autoComplete="off">
          <TextField
            onChange={handleChange('name')}
            id="outlined-full-width"
            label="Predict"
            style={{ margin: 20 }}
            className={classes.textField}
            placeholder="What is love?"
            margin="normal"
            variant="outlined"
          />
          <Button variant="contained" color="primary" onClick={handleSubmit} >
          Predict
          </Button>
        </form>

        <Dialog
          open={open}
          TransitionComponent={Transition}
          keepMounted
          onClose={handleClose}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"
        >
          <DialogTitle id="alert-dialog-slide-title">{"Result:"}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-slide-description">
              {values.result}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} color="primary">
              Dismiss
            </Button>
          </DialogActions>
        </Dialog>
      </header>
    </div>
  );
}

const useStyles = makeStyles(theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  dense: {
    marginTop: theme.spacing(2),
  },
  menu: {
    width: 200,
  },
}));

export default App;
